import { createSlice } from '@reduxjs/toolkit';
 const Allposts = createSlice({
    name: "Allpost",
    initialState: [] ,
    reducers: {
        GetAll : (state,action)=> {
             return action.payload;
        },
    }
})
export const GetAllposts = Allposts.actions
export default Allposts.reducer;