import { configureStore } from '@reduxjs/toolkit';
import { Auth } from './slices/Auth';
import { counter } from './slices/counter';
import { like } from './slices/like';
import Userinfo from "./slices/Userinfo"
import Getusers from "./slices/Getusers"
import Posts from "./slices/Posts"
import AllNotifications from "./slices/AllNotifications"
import Allposts from "./slices/Allposts"
import AllChat from "./slices/AllChat"
import OnlineUsers from "./slices/OnlineUsers"
import AllMessages from "./slices/AllMessages"
import Trendings from "./slices/Trendings"
import MyfollowingFollowers from "./slices/MyfollowingFollowers"
// import logger from 'redux-logger'

const store = configureStore({
    reducer: {
        counter: counter.reducer,
        like: like.reducer,
        authentic: Auth.reducer,
        userInfo : Userinfo,
        getusers:Getusers,
        posts : Posts,
        notifications:AllNotifications,
        Allpost:Allposts,
        trending:Trendings,
        myfollowingFollowers:MyfollowingFollowers,
        AllChat:AllChat,
        OnlineUsers:OnlineUsers,
        AllMessages:AllMessages,
    },
    // middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(logger),

});

export default store;
