import { createSlice } from '@reduxjs/toolkit';
export const counter = createSlice({
    name: "counter",
    initialState: { count: 0 },
    reducers: {
        increment(state, action) {
            state.count ++;
        },
        decrement(state, action) { 
            state.count --;
        },
        addBy(state, action) { 
            state.count += 10;
        }
    }
})
export const actions = counter.actions
