import { createSlice } from '@reduxjs/toolkit';
 const MyfollowingFollowers = createSlice({
    name: "myfollowingFollowers",
    initialState: [] ,
    reducers: {
        GetAll : (state,action)=> {
             return action.payload;
        },
    }
})
export const GetAllMyfollowingFollowers = MyfollowingFollowers.actions
export default MyfollowingFollowers.reducer;