import React, { useEffect, useState, useContext ,useCallback} from "react";
import { useDispatch } from "react-redux";
import { useLocation,Link } from "react-router-dom";
import { actions } from "../../redux/slices/counter";
import { uidContext } from "../AppContext";
import axios from "axios"
import { Avatar } from "@mui/material";
import { SERVER_URL } from "../../Domain";
import {Typography} from "@mui/material";
import { useSelector } from "react-redux";
import {isEmpty,timestampParser} from "../Utils"
const Header = () => {
  const uid = useContext(uidContext);
  const user = useSelector((state) => state.userInfo);
    const opened = useSelector((state) => state.authentic.connected);
  const dispatch = useDispatch();
  const location = useLocation();
  const [loading,setloading] = useState(true)
  const [ismessage, setIsmessage] = useState(true);
  const [searchResult, setSearchResult] = useState([])
  const [NotificationList, setNotificationList] = useState([]);
  const [input,setInput] = useState("")
  const [search,setSearch] = useState(false)
  const [loadingSearch,setloadingSearch] = useState(true)
  const [windowWidth,setwindowWidth] = useState(window.innerWidth)
  window.addEventListener("resize",()=>{
    setwindowWidth(window.innerWidth)
    return ()=> window.removeEventListener("resize",false)
},true)
  useEffect(() => {
    if (
      location.pathname === "/signup"||
      (location.pathname === "/messenger" && windowWidth < 602)||
      location.pathname === "/account-verification"||
      location.pathname.includes("/Change-password")||
       location.pathname.includes("settings/reset/")
      ) {
      setIsmessage(false);
    } else {
      setIsmessage(true);
    }
  }, [location.pathname,windowWidth]);
  const increment = () => {
    dispatch(actions.increment());
  };

  useEffect(()=>{
    if(opened){
        const fetchUserData = async () => {
      if (uid) {
        await axios({
          method: "get",
          url: `${SERVER_URL}/api/user/${uid}`,
          withCredentials: true,
        })
          .then(({data}) => {
            setloading(false)
          
          setNotificationList(data.notifications.reverse())
          })
          .catch((err) =>   console.log(err))
      }
    }
    fetchUserData()
  }

  },[opened,uid])
  //=========================search query=================================== 
const getHistory = async(e)=>{
  var valueinput = e.target.value
  valueinput.trim();
      if(isEmpty(valueinput) || valueinput.replace(/\s+/,"").length ){
      setSearch(false);
      setInput(e.target.value)
    }
  if(valueinput.replace(/\s+/,"").length){
      if(valueinput.length > 0){
        setSearch(true)
         await axios({
        method: "get",
        url: `${SERVER_URL}/api/post/search/post?search=${valueinput}`,
        withCredentials: true,
      })
        .then(({ data }) => {
        setSearchResult(data)
        console.log(data)        
        })
        .catch((error) => {
          console.log(error)
    }) 
      }
    }
}
  return (
    <div>
      {ismessage ? (
        <nav>
          <div className="container">
            <h2 className="logo">
            <Link to="/">chat-burundi</Link>
            </h2>
            {uid !=="no token" && uid !==null ? (
              <>
                <div className="search-bar">
                  <i className="ri-search-line"></i>
                  <input type="search" placeholder="search something" id="input" value={input} onChange = {(e)=>getHistory(e)} />
                </div>
                <div className="create">
                  {location.pathname === "/" && 
                  <label
                    className="btn btn-primary"
                    htmlFor="create-post"
                    onClick={increment}
                  >
                    create
                  </label> }
                  {location.pathname !== "/" && 
                  <label
                    className="btn btn-primary disableButton"
                  >
                    create
                  </label> }
                  <div className="profile-picture">
                    <Avatar src={user.picture} alt="profile1" />
                  </div>
                </div>{" "}
              </>
            ) : (
              <div className="create"></div>
            )}
          </div>

          {
            search && <div className="searchPopup">
            {
            input && <div>
            <i className="ri-search-line"></i>
            <p>{input > 50 ? input?.substr(0,55) + "...":input}</p>
            </div>
            }
            {
              searchResult?.map((item)=>(
                <>


                 {item.message &&
                 <div>
                  <i className="ri-search-line"></i>
                 <p><Link className="linkProfile" to={`/profile/${item._id}`}> {item.message.length > 50 ?
                    item.message.substr(0,55) + "...":item.message}</Link></p>
                 </div>
                 }
                 {item.pseudo &&
                  <div>
                 <i className="ri-search-line"></i>
                 <p><Link className="linkProfile" to={`/profile/${item._id}`}> {item.pseudo}</Link></p>
                  </div>
                 }
                 </>
                ))
            }
          </div>

          }
          

          <>
          {opened &&
          <div className="notifictions">
          <h1>Notifiction</h1>
          {!loading &&
            !isEmpty(NotificationList) &&
            NotificationList.map((notification) => {
              if (notification.notifieId !==uid) {
              return <div key={notification._id}>
                <div className="profile-picture">
                  <Avatar src={notification.pictureNotifie} alt="ass" />
                </div>
                <div className="notification-body">
                    <b>{notification.notifiePseudo} </b>  <Typography variant="caption">{notification.notification}</Typography> 
                  <small><Typography variant="caption">{timestampParser(notification.timestamp)}</Typography> </small>
                </div>
                </div> 
              }
            })
          }
              
          </div>
        }
          </>
        </nav>
      ) : (
        ""
      )}

    </div>
  );
};
export default Header;
