import { createSlice } from '@reduxjs/toolkit';
export const like = createSlice({
    name: "like",
    initialState: { likes: 0 },
    reducers: {
        increment(state, action) {
            state.likes ++;
        },
        decrement(state, action) { 
            state.likes --;
        },
    }
})
export const actions = like.actions
