import { Routes, Route, Navigate } from "react-router-dom";
import React ,{ useState, useEffect,Suspense } from "react"
import socketIO from "socket.io-client";
import { SERVER_URL } from "../Domain";
import { useDispatch } from "react-redux";
import { GetOnlineUsers } from "../redux/slices/OnlineUsers";
import Skeleton from "../commponents/Skeleton/Skeleton";
import Loading from "../loading/Loading"
// import Home from "../pages/Home";
// import Login from "../pages/Login";
// import Signup from "../pages/Signup";
// import Profile from "../pages/Profile";
// import Image from "../pages/Image";
// import Notfound from "../pages/Notfound";
// import Dashboard from "../pages/Dashboard";
// import Settings from "../pages/Settings";
// import Messenger from "../pages/Messenger";
// import ResetPassword from "../pages/ResetPassword";
// import Validation from "../pages/Validation";
// import Forgetpassword from "../pages/Forgetpassword";
const fakeDalay = async (promise) => {
  await new Promise((resolve) => {
    setTimeout(resolve, 3000);
  });
  return promise;
}

const Home = React.lazy(() => fakeDalay(import("../pages/Home")))
const Login = React.lazy(() => import("../pages/Login"))
const Signup = React.lazy(() => import("../pages/Signup"))
const Profile = React.lazy(() => import("../pages/Profile"))
const Image = React.lazy(() => import("../pages/Image"))
const Notfound = React.lazy(() => import("../pages/Notfound"))
const Settings = React.lazy(() => import("../pages/Settings"))
const Messenger = React.lazy(() => import("../pages/Messenger"))
const ResetPassword = React.lazy(() => import("../pages/ResetPassword"))
const Validation = React.lazy(() => import("../pages/Validation"))
const Forgetpassword = React.lazy(() => import("../pages/Forgetpassword"))
const Dashboard = React.lazy(() => import("../pages/Dashboard"))
const ProfileOther = React.lazy(() => import("../pages/ProfileOther"))


const Routers = () => {
  const dispatch = useDispatch()
  const [socket, setSocket] = useState(null)
  const user = localStorage.getItem("token");
  useEffect(() => {
    setSocket(socketIO.connect(SERVER_URL));
  }, [])
  useEffect(() => {
    socket?.on("onLineusers", (onLineusers) => dispatch(GetOnlineUsers.GetAll(onLineusers)))
  }, [socket, dispatch])
  return (
    <Routes>
      <>
        {user && <Route exact path="/" element={<Suspense fallback={<Skeleton/>}><Home socket={socket} /></Suspense>} />}
        <Route exact path="login" element={<Suspense fallback={<Loading/>}><Login /></Suspense>} />
        <Route exact path="/" element={<Suspense fallback={<Loading/>}><Navigate replace to="/login" /></Suspense>} />
        <Route exact path="signup" element={<Suspense fallback={<Loading/>}><Signup /></Suspense>} />
        <Route exact path="profile/me" element={<Suspense fallback={<Loading/>}><Profile socket={socket} /></Suspense>} />
        <Route exact path="profile/:id" element={<Suspense fallback={<Loading/>}><ProfileOther socket={socket} /></Suspense>} />
        <Route exact path="single/post/:id" element={<Suspense fallback={<Loading/>}><Image socket={socket} /></Suspense>} />
        <Route exact path="dashboard" element={<Suspense fallback={<Loading/>}><Dashboard /></Suspense>} />
        <Route exact path="settings" element={<Suspense fallback={<Loading/>}><Settings /></Suspense>} />
        <Route
          exact
          path="settings/reset/:emailCrypted"
          element={<Suspense fallback={<Loading/>}><ResetPassword /></Suspense>}
        />
        {user && <Route exact path="messenger" element={<Suspense fallback={<Loading/>}><Messenger/></Suspense>} />}
        <Route exact path="/messenger" element={<Suspense fallback={<Loading/>}><Navigate replace to="/login" /></Suspense>} />
        <Route exact path="/account-verification" element={<Suspense fallback={<Loading/>}><Validation /></Suspense>} />
        <Route
          exact
          path="/Change-password/:email"
          element={<Suspense fallback={<Loading/>}><Forgetpassword /></Suspense>}
        />
        <Route path="*" element={<Suspense fallback={<Loading/>}><Notfound /></Suspense>} />
      </>
    </Routes>
  );
};
export default Routers;
