import { createSlice } from '@reduxjs/toolkit';
const Getusers = createSlice({
    name: "getusers",
    initialState: {},
    reducers: {
        GetuAllsers: (state, action) => {
            return action.payload
        },
    },
})
export const getusersAction = Getusers.actions
export default Getusers.reducer;