import { createSlice } from '@reduxjs/toolkit';
const Userinfo = createSlice({
    name: "userInfo",
    initialState: {},
    reducers: {
        fetchUserInfo: (state, action) => {

            return action.payload
        },
        ChangeProfile : (state,action)=>{
            return {
                ...state,
                picture : action.payload
            }
        },
        ChangeProfileThumber : (state,action)=>{
            return {
                ...state,
                thumber : action.payload
            }
        }
        ,
        Follow: (state, action) => {
            return{
              ...state,
              following :[action.payload, ...state.following]

            }
        },
        UnFollow: (state, action) => {
             return {
                ...state,
                following: state.following.filter((users) => users !== action.payload)
             }

        },
        SavePost :(state,action)=>{
           return{
            ...state,
            saved:[action.payload,...state.saved]
           }
        },
        UnsavePost :(state,action)=>{
            return{
                ...state,
                saved:state.saved.filter((id)=>id !== action.payload)
            }
        }
    },
})
export const UserinfoAction = Userinfo.actions
export default Userinfo.reducer;